import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import './Login.css';

const Login: React.FC = () => {
  const [identifier, setIdentifier] = useState('');
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [isNewUser, setIsNewUser] = useState(false);

  const { login, user } = useAuth();
  const navigate = useNavigate();

  // Limpiar campos al cambiar entre modos
  useEffect(() => {
    setIdentifier('');
    setPassword('');
    setEmail('');
    setError('');
  }, [isNewUser]);

  // Redireccionar si ya est autenticado
  useEffect(() => {
    if (user) {
      // Si el usuario no ha completado su registro, redirigir a la pgina de completar registro
      if (!user.registrationComplete) {
        navigate('/complete-registration');
      } else {
        // Si ya complet su registro, redirigir a la pgina principal
        navigate('/');
      }
    }
  }, [user, navigate]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // Validaciones bsicas
    if (!identifier || !password) {
      setError('Por favor complete todos los campos');
      return;
    }

    if (isNewUser) {
      if (identifier.length !== 10 || !/^\d+$/.test(identifier)) {
        setError('La cdula debe tener 10 dgitos');
        return;
      }
      
      if (!email) {
        setError('El correo electrnico es obligatorio para recuperar su contrasea');
        return;
      }
      
      if (!/\S+@\S+\.\S+/.test(email)) {
        setError('Por favor ingrese un correo electrnico vlido');
        return;
      }
    }

    try {
      setError('');
      setLoading(true);
      
      const result = await login(identifier, password, isNewUser, email);
      
      if (!result.success) {
        setError(result.error || 'Error al iniciar sesin');
      }
    } catch (err) {
      setError('Ocurri un error inesperado');
      console.error('Error en inicio de sesin:', err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <h2 className="login-title">
        {isNewUser ? 'Registro de Nuevo Usuario' : 'Inicio de Sesin'}
      </h2>
      
      <div className="mode-selector">
        <button 
          className={`mode-button ${!isNewUser ? 'mode-button-active' : ''}`}
          onClick={() => setIsNewUser(false)}
        >
          Iniciar Sesin
        </button>
        <button 
          className={`mode-button ${isNewUser ? 'mode-button-active' : ''}`}
          onClick={() => setIsNewUser(true)}
        >
          Registrarse
        </button>
      </div>
      
      {error && <div className="error-message">{error}</div>}
      
      <form className="login-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label className="form-label" htmlFor="identifier">
            {isNewUser ? 'Cdula (10 dgitos)' : 'Nombre de Usuario'}
          </label>
          <input
            type="text"
            id="identifier"
            className="form-input"
            value={identifier}
            onChange={(e) => setIdentifier(e.target.value)}
            placeholder={isNewUser ? "Ingrese su cdula" : "Ingrese su nombre de usuario"}
            disabled={loading}
          />
          {isNewUser && (
            <div className="form-help">Ingrese su nmero de cdula sin guiones ni espacios</div>
          )}
        </div>
        
        {isNewUser && (
          <div className="form-group">
            <label className="form-label" htmlFor="email">Correo Electrnico</label>
            <input
              type="email"
              id="email"
              className="form-input"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Ingrese su correo electrnico"
              disabled={loading}
            />
            <div className="form-help">Necesario para recuperar su contrasea</div>
          </div>
        )}
        
        <div className="form-group">
          <label className="form-label" htmlFor="password">Contrasea</label>
          <input
            type="password"
            id="password"
            className="form-input"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Ingrese su contrasea"
            disabled={loading}
          />
          {isNewUser && (
            <div className="form-help">Para su primer ingreso, use su cdula como contrasea inicial</div>
          )}
        </div>
        
        <button 
          type="submit" 
          className="submit-button"
          disabled={loading}
        >
          {loading ? 'Procesando...' : (isNewUser ? 'Registrarse' : 'Ingresar')}
        </button>
      </form>
      
      <div className="register-link">
        {isNewUser ? (
          <p>Al registrarse, podr completar su perfil en el siguiente paso.</p>
        ) : (
          <>
            <p>No tiene una cuenta? <a href="#" onClick={(e) => { e.preventDefault(); setIsNewUser(true); }}>Registrarse</a></p>
            <p><Link to="/forgot-password">Olvid su contrasea?</Link></p>
          </>
        )}
      </div>
    </div>
  );
};

export default Login; 